import { mapState } from 'vuex';
export default{
    props: ['shopTypeList'],
    data() {
        return {
        };
    },
    methods: {
        goSearch(item) {
            // console.log($(el).attr('value')); // 得到了typeValue
            this.$store.state.searchTypeValue = item.typeValue; // 传入vuex中
            this.$store.state.searchTypeName = item.typeZh; // 传入vuex中
            this.$router.push({path: '/shops'});  // 跳转到商户搜索页
        }
    },
    computed: {
        ...mapState([
            'global'
        ])
    }
};



