import nav from '@/components/nav/nav.vue';
import header from '@/components/index/header.vue';
import swiperclick from '@/components/swiper/swiperclick.vue';
import column from '@/components/index/column.vue';
import indexcoupon from '@/components/coupon/indexcoupon.vue';
import { mapState } from 'vuex';
import loadGif from '@/components/loadGif/loadGif.vue';

export default {
    head() {
        return {
            title: '惠付券'
        };
    },
    beforeCreate() {
    },
    data() {
        return {
            shopTypeList: [],
            couponList: [],
            currentPage: 0,
            totalPage: Number,
            nextPage: Number,
            position: '',
            page: 1, // 搜索用的page
            busy: true,
            showLoadGif: true,
            swiperinfo: [
                {
                    url: this.$store.state.global.hftcom + '/huiquan/static/img/a316d77207e3466e811d099d19e9a05886d442e14d086-qQwXw7_fw658.jpg'
                    // activityWeb: ''
                }
            ]
        };
    },
    computed: {
        ...mapState([
            'shopDetail', 'CouponListInfo', 'localCity', 'isChooseCity', 'global', 'weixin', 'zoneId'
        ])
    },
    watch: {
        // 监听localCity的变化
        localCity: function(newval, oldval) {
            this.page = 1;
            this.couponList = [];
            this.getshopTypeList();  // 分类信息
            this.getCouponList();    // 优惠券列表
            this.getcouponSwiper();  // 优惠券轮播图
            this.$store.dispatch('getCollectInfo');  // 收藏信息
        }
    },
    methods: {
        getcouponSwiper() {
            // 轮播图
            this.axios({
                method: 'post',
                url: this.$store.state.global.hftcomShop,
                data: '{"id":19,"jsonrpc":"2.0","method":"couponSwiper","params":{"city":"' + this.localCity + '","zoneId":"' + this.zoneId + '"}}',
                dataType: 'json'
            }).then((result) => {
                if (result.data.result.length >= 1) {
                    this.swiperinfo = result.data.result;
                    for (let index = 0; index < this.swiperinfo.length; index++) {
                        this.swiperinfo[index].url = this.global.hftcom + this.swiperinfo[index].url;
                    }
                }
            });
        },
        ListIsNull() {
            if (this.couponList.length < 1) {
                this.showLoadGif = false;
                this.$vux.toast.text('当前没有优惠券', 'bottom');
            }
        },
        hiddenLoadGif() {
            if (this.nextPage <= this.currentPage) {
                // this.$vux.toast.text('加载完毕', 'bottom');
                this.showLoadGif = false;
            }
        },
        getshopTypeList() {  // 获得栏目列表
            this.axios({
                method: 'post',
                url: this.$store.state.global.hftcomClient,
                data: '{"id":19,"jsonrpc":"2.0","method":"getClientHomePage","params":{"city":"' + this.$store.state.localCity + '","zoneId":"' + this.zoneId + '"}}',
                dataType: 'json'
            }).then((result) => {
                this.$data.shopTypeList = result.data.result.shopTypeList;
            });
        },
        getCouponList() {  // 获得优惠券列表的方法
            this.showLoadGif = true;
            this.axios({
                method: 'post',
                url: this.global.hftcomClient,
                data: '{"id":19,"jsonrpc":"2.0","method":"listCoupon","params":{"couponType":"0","searchWord":"","longitude":"' + this.$store.state.longitude + '","latitude":"' + this.$store.state.latitude + '","page":"' + this.page + '","city":"' + this.$store.state.localCity + '","userCode":"' + this.$store.state.userCode + '","zoneId":"' + this.zoneId + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (result.data.result.couponList.CouponListInfo) {
                    this.couponList = [...this.$data.couponList, ...result.data.result.couponList.CouponListInfo];
                    this.currentPage = result.data.result.page;
                    this.nextPage = result.data.result.nextPage;
                    this.busy = false;
                    this.page ++;
                }
                this.ListIsNull();
                this.hiddenLoadGif();
            });
        },
        loadMore() {
            setTimeout(function() {}, 5000);
            this.busy = true;

            if (this.nextPage > this.currentPage) {
                this.busy = false;
                this.getCouponList();
            } else {
                this.busy = true;
                // this.$vux.toast.text('加载完毕', 'bottom');
                this.showLoadGif = false;
            }
        }
    },
    created () {
        this.getshopTypeList();  // 分类信息
        this.getCouponList();    // 优惠券列表
        this.getcouponSwiper(); // 获得轮播图
    },
    mounted() {
        this.$store.dispatch('getCollectInfo'); // 更新获得优惠券的收藏信息，用于筛选收藏按钮
    },
    components: {
        'v-nav': nav,
        'v-header': header,
        swiperclick,
        column,
        indexcoupon,
        loadGif
    }};



